
@import url('../assets/iconfont/iconfont.css');
.vm-editor{
  background-color: white;
  border-radius: 4px;
  border: 1px solid #eeeff1;
  width: 100%;
  min-width: 900px;
  overflow: hidden;
}
.global-control{
  position: absolute;
  right: 15px;
}
.vm-editor .content{
    outline: 0;
    min-height: 350px;
    text-align: left;
    padding: 15px;
    font-size: 16px;
}
.vm-editor .content ul, .vm-editor .content ol{
  margin: 10px 20px;
  padding: 0;
}
.vm-editor .content ul{
  list-style-type: square;
}
.vm-editor .content ol{
  list-style-type: decimal;
}
.vm-editor .content li{
  display: list-item;
  padding: 0;
}
.vm-editor .content hr{
  margin: 15px 0;
  border-top: 1px solid #eeeff1;
}
.vm-editor .content pre{
  display: block;
  margin: 10px 0;
  padding: 8px;
  border-radius: 4px;
  background-color: #f2f2f2;
  color: #656565;
  font-size: 14px;
}
.vm-editor .content blockquote{
  display: block;
  border-left: 4px solid #ddd;
  margin: 15px 0;
  padding: 0 15px;
}
.vm-editor .content img{
  margin: 20px 0;
}
.vm-editor .content a{
  color: #41b883;
}



